<template>
  <div>
    <v-menu offset-y
            v-model="isShow"
            :close-on-content-click='false'>
      <template v-slot:activator="{ on, attrs }">
        <div>
          <v-text-field v-if="!isShow"
                        v-bind="attrs"
                        v-on="on"
                        :label="label || $t('label_Email_message')"
                        :readonly='readonly'
                        :value="defaultemailsubject"
                        append-icon="mdi-longitude">
          </v-text-field>
          <h4 v-else
              :style="{color: $vuetify.theme.defaults.light.primary}">{{label}}</h4>
        </div>
      </template>
      <v-list class="bigbox">
        <v-list-item v-for="(item, index) in childrenpurport"
                     :key="index">
          <v-text-field :label="$t(`label.${item.language_code}`)+'*'"
                        :rules='rules'
                        @blur="handleinputBlur"
                        :readonly='readonly'
                        v-model="item.content">
          </v-text-field>
          <propertyDialog :readonly='!readonly'
                          @getpropertylist='getpropertylist($event,index)'></propertyDialog>
        </v-list-item>
        <div class="btnbox"
             v-show="!readonly">
          <mrc_ess_submitBtn @cancel='cancel'
                             @success='success'
                             cancelText='cancel'
                             successText='OK'></mrc_ess_submitBtn>
        </div>
      </v-list>
    </v-menu>

    <!-- <v-text-field v-if="!isShow"
                  :label="label"
                  :readonly='readonly'
                  v-model="defaultemailsubject"
                  @click='changeisShow'
                  append-icon="mdi-longitude">
    </v-text-field>
    <h4 v-else
        style="color: #0F46C1">{{label}}</h4> -->

    <!-- inout弹出框 -->
    <!-- <div class="inputbox"
         :style="{display:isShow?'block':'none'}">
      <div v-for="(item,index) in childrenpurport"
           :key="index+item">
        <v-text-field :label="$t(`label.${item.language_code}`)"
                      @blur="handleinputBlur"
                      :readonly='readonly'
                      v-model="item.content">
        </v-text-field>
        <propertyDialog :readonly='!readonly'
                        @getpropertylist='getpropertylist($event,index)'></propertyDialog>
      </div>
      <div class="btnbox">
        <mrc_ess_submitBtn @cancel='cancel'
                           @success='success'
                           cancelText='cancel'
                           successText='OK'></mrc_ess_submitBtn>
      </div>
    </div> -->
  </div>
</template>

<script>
import propertyDialog from './propertyDialog'
export default {
  components: {
    propertyDialog
  },
  props: {
    label: {
      type: String
    },
    purport: {
      type: Array,
      default: () => []
    },
    defaultemailsubject: {
      type: String
    },
    readonly: {
      type: Boolean,
      default: false
    }
  },
  model: {
    prop: 'purport',
    event: 'purportChange'
  },
  data () {
    return {
      rules: [
        value => !!value || 'Required.',
      ],
      isShow: false,
      childrenpurport: [],
      blurIndex: null
    };
  },
  computed: {},
  created () { },
  watch: {
    purport: {
      handler (v) {
        this.childrenpurport = this.$clone(v)
      }, deep: true
    }
  },
  mounted () {
    this.childrenpurport = this.$clone(this.purport)
  },

  methods: {
    success () {
      this.isShow = false
      this.$emit('purportChange', this.childrenpurport)
    },
    cancel () {
      this.isShow = false
      this.childrenpurport = this.$clone(this.purport)
    },
    getpropertylist (v, index) {
      v.map(item1 => {
        this.childrenpurport[index].content ? this.childrenpurport[index].content = this.childrenpurport[index].content.slice(0, this.blurIndex) + item1.property + this.childrenpurport[index].content.slice(this.blurIndex) : this.childrenpurport[index].content = item1.property
      })
    },
    handleinputBlur (e) {
      this.blurIndex = e.srcElement.selectionStart
    }
  },
  validations: {

  }
};
</script>

<style scoped>
