  width: 100%;
  background-color: #ffffff;
  padding: 30px 30px;
  box-shadow: -10px 10px 3px #eeeef0, 10px 10px 3px #eeeef0;
} */
.btnbox {
  display: flex;
  flex-direction: row-reverse;
}
.btnbox .btnGroup {
  background-color: #ffffff;
  right: 10px;
  margin: 0;
  margin-top: 20px;
  padding-right: 20px;
}
.bigbox /deep/ .v-list-item {
  display: block;
  margin-bottom: 15px;
}
.bigbox /deep/ .v-.v-messages {
  min-height: 0px;
}
</style>
